import React from 'react';
import { Square } from '../square/square.component'
export class Board extends React.Component {

  renderSquare(i) {
    return <Square className = "square" key={i} value={this.props.values[i]} 
    handleClick={() => this.props.handleClick(i,this.props.time)} />;
  }

  render() {
    return (
      <div>
        <div className="board-row">
          {this.renderSquare(0)}
          {this.renderSquare(1)}
          {this.renderSquare(2)}
        </div>
        <div className="board-row">
          {this.renderSquare(3)}
          {this.renderSquare(4)}
          {this.renderSquare(5)}
        </div>
        <div className="board-row">
          {this.renderSquare(6)}
          {this.renderSquare(7)}
          {this.renderSquare(8)}
        </div>
      </div>
    );
  }
}